import './PartnerList.css';
import { useState, useEffect } from 'react';
import db from '../../services/firebase.js';
import PartnerCard from '../PartnerCard/PartnerCard.jsx';
import { collection, onSnapshot } from 'firebase/firestore';

export default function PartnerList({ _area, search }) {
  const [unityList, setUnityList] = useState([]);
  const [openItemId, setOpenItemId] = useState(null);

  const normalize = (str) =>
    (str || "").normalize('NFD').replace(/[\u0300-\u036f]/g, '').toLowerCase();

  const normalizedSearch = normalize(search);

  const imma = {
    name: "Clínica Imma",
    id: "Clinica Imma",
    area: "medica",
    endereco: {
      unidade1: {
        bairro: "Monte Castelo",
        complemento: "Av. Getúlio Vargas",
        numero: "2509"
      },
      unidade2: {
        bairro: "",
        complemento: "",
        numero: ""
      }
    },
    desconto: "Até 70%",
    contato: {
      0: "98 33110101",
      1: "98 98542-4532"
    }
  };

  useEffect(() => {
    const unsubscribe = onSnapshot(collection(db, "credenciados"), (snapshot) => {
      const allUnits = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));

      const filteredUnits = allUnits.filter((unity) => {
        const nameMatch = normalize(unity.name).includes(normalizedSearch);
        if (normalizedSearch) {
          return nameMatch; // busca global
        } else {
          return unity.area === _area; // sem busca → filtra por área
        }
      });

      setUnityList(filteredUnits);
    });

    return () => unsubscribe();
  }, [_area, search]);

  function changesItemDisplay(_item) {
    setOpenItemId((prev) => (prev === _item ? null : _item));
  }

  const showImma =
    (normalizedSearch === "" && _area === "medica") ||
    (normalizedSearch !== "" && normalize(imma.name).includes(normalizedSearch));

  return (
    <div className="partner-list">
      {showImma && (
        <PartnerCard
          isOpen={openItemId === `999-${imma.area}`}
          onClick={changesItemDisplay}
          unit={imma}
          id="999"
          key={imma.id}
        />
      )}
      {unityList.map((unit, index) => (
        <PartnerCard
          isOpen={openItemId === `${index + 1}-${unit.area}`}
          onClick={changesItemDisplay}
          id={index + 1}
          key={unit.id}
          unit={unit}
        />
      ))}
    </div>
  );
}
